/* Do not remove the comments below. It's the markers used by gulp-inject to inject
   all your sass files automatically */
#site-wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
}

#site-canvas {
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.show-nav #site-canvas {
  -webkit-transform: translateX(18.75em);
  transform: translateX(18.75em);
}

#site-wrapper.show-nav #site-canvas {
  -webkit-transform: translateX(18.75em);
  transform: translateX(18.75em);
}

.topnav {
  width: 100%;
  float: left;
  color: #fff;
  background: #01bf9d;
}
.topnav__icon {
  position: relative;
  width: 1.8em;
  height: 1.8em;
  margin: .5em .5em .5em .75em;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
  cursor: pointer;
  z-index: 1;
}
.topnav__icon--item {
  position: absolute;
  width: 1.75em;
  height: .25em;
  background-color: #fff;
  -webkit-transition: all 300ms linear;
  transition: all 300ms linear;
}
.topnav__icon .one {
  top: .125em;
}
.topnav__icon .two {
  top: .75em;
  opacity: 1;
}
.topnav__icon .three {
  top: 1.375em;
}
.topnav__icon.cross {
  -webkit-transform: translateX(-4em);
  transform: translateX(-4em);
}
.topnav__icon.cross .one {
  -webkit-transform: translateY(0.625em) rotate(45deg);
  transform: translateY(0.625em) rotate(45deg);
}
.topnav__icon.cross .two {
  opacity: 0;
}
.topnav__icon.cross .three {
  -webkit-transform: translateY(-0.625em) rotate(-45deg);
  transform: translateY(-0.625em) rotate(-45deg);
}

.ms-menu {
  position: absolute;
  width: 18.75em;
  height: 100%;
  top: 0;
  left: -18.75em;
  background: #01bf9d;
  padding: 46px 0px 0px;
  box-shadow: inset -3px 0px 5px -3px rgba(0, 0, 0, 0.75);
  list-style: none;
}
.ms-menu > li {
  position: relative;
}
.ms-menu > li > a {
  color: #fff;
  display: block;
  padding: 20px 14px;
  border-left: 5px solid #01bf9d;
  text-decoration: none;
}
.ms-menu > li > .sub_Menu {
  position: relative;
  width: 100%;
  top: 100%;
  z-index: 10;
  background: #01a185;
  box-shadow: inset -2px 0px 5px -2px rgba(0, 0, 0, 0.75);
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.5s ease-in-out;
  transition: height 0.5s ease-in-out;
}
.ms-menu > li > .sub_Menu .sub_Link {
  padding-left: 5px;
}
.ms-menu > li > .sub_Menu .sub_Link a {
  color: #fff;
  display: block;
  padding: 20px 14px;
  text-decoration: none;
}
.ms-menu > li > .sub_Menu .ms-menu__active {
  padding-left: 0px;
  border-left: 5px solid #fff;
}
.ms-menu > li > .sub_Menu.expandItem {
  height: 18.4375em;
}
.ms-menu__active .linkName {
  color: #fff;
  border-left: 5px solid #fff;
}



